<template>
    <el-container class="home-container">
        <el-header>
            <div class="logo-box">
                <img src="../../assets/images/heima.png" alt="">
                <span>电商后台管理系统</span>
            </div>
            <el-button type="info" @click="logout">退出</el-button>
        </el-header>
        <el-container>
            <el-aside :width="collapse ? '65px' : '200px'">
                <div class="toggle_bar" @click="collapse = !collapse" >|||</div>
                <el-menu background-color="#333744" text-color="#fff" active-text-color="#409eff" :unique-opened="true" :collapse="collapse" :collapse-transition="false" :router="true">
                    <el-submenu :index="item.id + ''" v-for="(item, i) in menulist" :key="item.id" :style="collapse ? 'width: 65px;' : 'width: 200px;'">
                        <template slot="title">
                            <i :class="['iconfont',iconList[i]]"></i>
                            <span>{{item.authName}}</span>
                        </template>
                        <el-menu-item :index="'/' + subItem.path" v-for="subItem in item.children" :key="subItem.id">
                            <i class="el-icon-menu"></i>
                            <span>{{subItem.authName}}</span>
                        </el-menu-item>
                    </el-submenu>
                </el-menu>
            </el-aside>
            <el-main>
                <router-view></router-view>
            </el-main>
        </el-container>
    </el-container>
</template>

<script>
import mix from './Home-mixins'
export default {
  mixins: [mix]
}
</script>

<style lang="less" scoped>
.home-container {
  height: 100%;
  .el-header {
    background-color: #373d41;
    height: 60px;
    display: flex;
    justify-content: space-between;
    padding: 0;
    align-items: center;
    padding-right: 20px;
    .logo-box {
      display: flex;
      color: white;
      font-size: 22px;
      align-items: center;
      user-select: none;
      img {
        width: 50px;
        height: 50px;
        margin-right: 10px;
      }
    }
  }
  .el-aside {
    background-color: #333744;
  }
  .el-main {
    background-color: #eaedf1;
  }
}
.toggle_bar {
  color: #fff;
  font-size: 12px;
  text-align: center;
  line-height: 25px;
  background-color: #4a5064;
  user-select: none;
  cursor: pointer;
  letter-spacing: 0.1em;
}
</style>



