export default {
  data() {
    return {
      rolesList: [],
      addDialogVisible: false,
      addForm: {
        roleName: '',
        roleDesc: ''
      },
      addFormRules: {
        roleName: [{ required: true, message: '请输入角色名称', trigger: 'blur' }],
        roleDesc: [{ required: true, message: '请输入角色描述', trigger: 'blur' }]
      },
      editFormVisible: false,
      editform: {
        roleName: '',
        roleDesc: ''
      },
      editFormRules: {
        roleName: [{ required: true, message: '请输入角色名称', trigger: 'blur' }],
        roleDesc: [{ required: true, message: '请输入角色描述', trigger: 'blur' }]
      },
      setRightDialogVisible: false,
      rightTree: [],
      treeProp: {
        label: 'authName',
        children: 'children'
      },
      defaultCheckedKeys: [],
      roleId: ''
    }
  },
  methods: {
    async getRolesList() {
      const { data: res } = await this.$http.get('roles')
      if (!res) return this.$message.error('获取角色列表失败')
      this.rolesList = res.data
    },
    async removeRight(role, rightId) {
      const confirmResult = await this.$confirm('此操作将永久删除该权限, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)

      if (confirmResult !== 'confirm') {
        return this.$message({
          type: 'info',
          message: '已取消删除'
        })
      }

      const { data: res } = await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
      if (res.meta.status !== 200) return this.$message.error('删除权限失败!')

      role.children = res.data
    },
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    addRoles() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return false
        const { data: res } = await this.$http.post('roles', this.addForm)
        if (res.meta.status !== 201) return this.$message.error('添加角色失败')
        this.$message.success('添加角色成功!')
        this.getRolesList()
        this.addDialogVisible = false
      })
    },
    async showEditDialog(id) {
      const { data: res } = await this.$http.get('roles/' + id)
      if (res.meta.status !== 200) return this.message.error('查询角色信息失败!')
      this.editform = res.data
      this.editFormVisible = true
    },
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    saveRolesInfo() {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return false
        const { data: res } = await this.$http.put('roles/' + this.editform.roleId, this.editform)
        if (res.meta.status !== 200) return this.$message.error('编辑角色信息失败!')
        this.$message.success('编辑角色信息成功')
        this.getRolesList()
        this.editFormVisible = false
      })
    },
    async removeRoles(id) {
      const confirmResult = await this.$confirm('此操作将永久删除该角色, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)

      if (confirmResult !== 'confirm') {
        return this.$message({
          type: 'info',
          message: '已取消删除'
        })
      }

      const { data: res } = await this.$http.delete('roles/' + id)
      if (res.meta.status !== 200) return this.$message.error('删除用户是失败')
      this.$message.success('删除用户成功!')
      this.getRolesList()
    },
    async showSetRightDialog(role) {
      this.roleId = role.id
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) return this.$message.error('获取所有权限失败!')
      this.rightTree = res.data
      const keys = []
      this.getLeafId(role, keys)
      this.defaultCheckedKeys = keys
      this.setRightDialogVisible = true
    },
    getLeafId(node, keyArr) {
      if (!node.children) {
        return keyArr.push(node.id)
      }
      node.children.forEach(item => this.getLeafId(item, keyArr))
    },
    async saveRights() {
      const k1 = this.$refs.tree.getCheckedKeys()

      const k2 = this.$refs.tree.getHalfCheckedKeys()

      const idstr = [...k1, ...k2].join(',')

      const { data: res } = await this.$http.post(`roles/${this.roleId}/rights`, { rids: idstr })

      if (res.meta.status !== 200) return this.$message.error('分配权限失败!')
      this.$message.success('分配权限成功!')
      this.getRolesList()
      this.setRightDialogVisible = false
    }
  },
  created() {
    this.getRolesList()
  }
}



