export default {
  data() {
    return {
      rightsList: []
    }
  },
  created() {
    this.getRightList()
  },
  methods: {
    async getRightList() {
      const { data: res } = await this.$http.get('rights/list')
      if (!res) return this.$message.error('获取权限列表失败')
      this.rightsList = res.data
    }
  }
}



